* {
    font-family: 'Montserrat', 'Gadugi', sans-serif;
}

body {
    margin: 0;
    background-color: white;
    height: auto;
}

#root {
    display: grid;
}

header {
    display: flex;
    justify-content: center;
    background-color: #388cc4;
    flex-wrap: wrap;
}

header img {
    height: auto;
    width: 15%;
}

header h1 {
    margin: auto 25px;
    color: white;
}

.app {
    margin: auto;
    width: 70%;
    display: grid;
    justify-content: center;
}

.project {
    width: auto;
    margin: 15px;
    padding: 25px;
    border-radius: 15px;
    background-color: rgb(228, 226, 226);
}

.project a {
    font-weight: bold;
    font-size: 17pt;
    color: #388DC4;
    text-decoration: none;
}

.project a:hover {
    cursor: pointer;
    text-decoration: underline;
}

.contacts {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: whitesmoke;
    padding: 20px 0;

    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.contacts a {
    margin: 5px 15px;
    display: flex;
    text-decoration: none;
    color: black;
}

.contacts a:hover {
    cursor: pointer;
    text-decoration: underline;
}

p {
    margin: auto 5px;
}

.contacts img {
    width: 40px;
    height: auto;
}

@media only screen and (max-width: 750px) {
    .app {
        width: 100%;
    }

    header h1 {
        margin: 15px auto;
        text-align: center;
    }

    header img {
        width: 25%;
    }
}

@media only screen and (max-width: 400px) {
    .contacts {
        position: relative;
    }

    header h1 {
        margin: 25px auto;
        text-align: center;
        font-size: 30pt;
    }

    header img {
        height: 275px;
        width: 100%;
        object-fit: cover;
        box-shadow: 0px 10px 15px 5px rgba(0, 0, 0, 0.59);
    }
}